body {
    margin: 0;
    padding: 0;
    height: 100%;
    font-family: sans-serif;
    background-color: white;
    /*background-color: #DCDCDC;*/
}

.main-body {
    margin-top: 75px;
}

/* Custom CSS to fix spacing between FA icons and text in the drawer */

.iconTexthome {
    margin-left: -3px;
}

.iconTextusers {
    margin-left: -5px;
}

.iconTexthand-holding-heart {
    margin-left: -3px;
}

.NoMatch-body {
  text-align: center;
  margin-top: 125px;
}

.home-main-pic {
    max-width: 98%;
    max-height: 98%;
    border: 5px solid black;
}

/* Styling for the Donate and Adopt buttons */
.button-icon {
    vertical-align: -10%;
    margin-right: 5px;
}

.adopt-button {
    border: 2px solid #F48D2C;
    color: #F48D2C;
    padding: 0px 15px;
    border-radius: 8px;
    background-color: #ffffff;
}

.adopt-button:hover {
  color: #343436;
  background-color: #F48D2C;
}

.donate-button {
    margin-right: 25px;
    border: 2px solid #8FC547;
    color: #8FC547;
    padding: 0px 15px;
    border-radius: 8px;
    background-color: #ffffff;
}

.donate-button:hover {
  color: #343436;
  background-color: #8FC547;
}

.about-pic {
  max-width: 98%;
  max-height: 98%;
  border: 5px solid black;
}

.contact-fa-icon-mail {
  margin-right: 8px;
  color: #F48D2C;
}

.contact-fa-icon {
  margin-right: 10px;
  color: #F48D2C;
}

.contact-pf-icon {
  position: relative;
  top: 2px;
  width: 15px;
  margin-right: 11px;
}

.dog-profile-pic {
    max-width: 98%;
    max-height: 98%;
    border: 5px solid black;
}

.donate-pic {
    max-width: 98%;
    max-height: 98%;
    border: 5px solid black;
}

